@import url('https://fonts.googleapis.com/css2?family=Jua&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
    margin: 0;
    padding: 0;
}
.navbarWrapper {
  position: fixed;
  width: 100%;
  height: 164px;
  z-index: 100;

}

.svgBackground {

  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.navLinks {
  position: absolute;
      font-family: 'Poppins', sans-serif;

  top: 24px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  display: flex;
  gap: 36px;
  align-items: center;
}

.navItem {
  font-size: 18px;
  text-decoration: none;
  transition: color 0.3s;
}

.contactUs {
  position: absolute;
  
  right: 46px;
  top: 20px;
  text-decoration: none;
      z-index: 100;

}


.but {
    padding: 8px 14px;
    font-size:16px;
    font-family: 'Poppins', sans-serif;
    background-color: #fcfcfc;
    border-radius: 10px;
    color: #2b2b2b;
    border: none;
    transition: .3s;
    cursor: pointer;
}
.but:hover {
    background-color: #2b2b2b;
    color: #fcfcfc;
    transition: .3s;
}
.logo{
    position: absolute;
    top: -20px;
    left: -16px;
    z-index: 100;
    width: 190px;


}
.text-wrapper-2{
  margin-top: 31px;

}
@media screen and (max-width: 768px){
  .logo{
    width: 99px;
    top: -10px;
    left: -4px;
  }
  .list{
    position: fixed;
    left: 80px;
    top: 16px;
    background-color: transparent;
    border: none;
    width: fit-content;
  }
  .mobileNavbar{
z-index: 100;
  position: fixed;
  }
  .contactUs {
  position: fixed;
  right: 8px;
  top: 8px;

}
.but {
    font-size: 14px;
    background-color: #A4EFFB;
 
}
}